.header {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  padding: 16px 50px;
  font-size: 20px;
  color: #fff;
  background-color: #ffffff;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .header {
    font-size: 26px;
  }
}
